import React from 'react';
import './styles/article.css';
import { Link } from "react-router-dom";
import moment from 'moment';
import Loading from './Loading';

class Article extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentPage: 1,
      totalPage: 1,
      data: [],
      api: 'api/article'
    };
    this.onDataFetched = this.onDataFetched.bind(this);
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_API_URL}/${this.state.api}`)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState(state => ({
            ...state,
            data: result.data,
            currentPage: result.currentPage,
            totalPage: result.totalPage,
          }));
        },
        (error) => {
          console.log(error);
        }
      )
  }

  onDataFetched(result) {
    let data = this.state.data.concat(result.data);
      this.setState(state => ({
        ...state,
        data: data,
        currentPage: parseInt(result.currentPage),
        totalPage: parseInt(result.totalPage),
    }));
  }

  render() {
    return (
      <div>
        <p>
          <Link to="/">首页</Link> 这里应该会有摘抄，碎碎念，无聊的流水账
        </p>
        {
          this.state.data.map((item) => 
            <p>
              {item.content}
              <div className="article-footer">
                <span className="time">{moment(item.created_at).format('YYYY-MM-DD')}</span>
                {/* <span className="action"> <a href="#">读了(0)</a></span> */}
              </div>
            </p>
          )
        }
        <Loading api={this.state.api} 
          onDataFetched={this.onDataFetched} 
          totalPage={this.state.totalPage} 
          currentPage={this.state.currentPage} />
      </div>
    );
  }
}

export default Article;
