import React from 'react';

class Loading extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loadingData: false,
    };
    // This binding is necessary to make `this` work in the callback
    this.loadingMore = this.loadingMore.bind(this);
  }

  loadingMore() {
    let nextPage = this.props.currentPage + 1;
    this.setState({
      loadingData: true
    });
    fetch(`${process.env.REACT_APP_API_URL}/${this.props.api}?page=${nextPage}`)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            loadingData: false
          });
          this.props.onDataFetched(result);
        },
        (error) => {
          console.log(error);
        }
      )
  }

  render () {
    return (
      <p>
        {
          this.props.totalPage > this.props.currentPage &&
          // eslint-disable-next-line jsx-a11y/anchor-is-valid
          <a href="#" onClick={this.loadingMore}>{this.state.loadingData ? '加载中...' : '加载更多'}</a>
        }
      </p>
    );
  }
}

export default Loading;
