import React from 'react';
import { Link } from "react-router-dom";
import audio from './images/audio.svg';
import './styles/music.css';
import moment from 'moment';
import Loading from './Loading';

class Music extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isPlaying: 0,
      currentPage: 1,
      totalPage: 1,
      data: [],
      api: 'api/music'
    };
    // This binding is necessary to make `this` work in the callback
    this.playMusic = this.playMusic.bind(this);
    this.onDataFetched = this.onDataFetched.bind(this);
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_API_URL}/${this.state.api}`)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState(state => ({
            ...state,
            data: result.data,
            currentPage: result.currentPage,
            totalPage: result.totalPage,
          }));
        },
        (error) => {
          console.log(error);
        }
      )
  }

  playMusic(id, link) {
    let isPlaying = this.state.isPlaying;
    let status = this.state.isPlaying;
    if (isPlaying !== 0) {
      this.audio.pause();
      status = 0;
    }
    if (isPlaying !== id) {
      this.audio.src = link;
      this.audio.play();
      status = id;
    }
    this.setState(state => ({
      ...state,
      isPlaying: status,
    }));
  }

  onDataFetched(result) {
    let data = this.state.data.concat(result.data);
      this.setState(state => ({
        ...state,
        data: data,
        currentPage: parseInt(result.currentPage),
        totalPage: parseInt(result.totalPage),
    }));
  }

  render () {
    return (
      <div>
        <p>
          <Link to="/">首页</Link> 这里会放对特别喜欢的歌的备注
        </p>
        {
          this.state.data.map((item, index) => 
            <p key={index}>
              <div><svg className="icon" onClick={this.playMusic.bind(this, item.id, item.link)}><use xlinkHref={this.state.isPlaying === item.id ? `${audio}#pause` : `${audio}#play-button`} /></svg>
              <span> {item.title} </span> - <span> {item.singer} </span></div>
              <div className="notes">{item.notes}</div>
              <div className="music-footer">
                <span className="time">{moment(item.created_at).format('YYYY-MM-DD')}</span>
              </div>
            </p>
          )
        }
        <Loading api={this.state.api} 
          onDataFetched={this.onDataFetched} 
          totalPage={this.state.totalPage} 
          currentPage={this.state.currentPage} />
        <audio ref={el => this.audio = el}></audio>
      </div>
    );
  }
}

export default Music;
