import React from 'react';
import { Link } from "react-router-dom";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: 0,
      count: 0
    };
  }

  componentDidMount() {
    fetch(`${process.env.REACT_APP_API_URL}/api/visitors`)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            id: result.id,
            count: result.count
          });
        },
        (error) => {
          console.log(error);
        }
      )
  }

  render() {
    return (
      <div>
        <p>
          你好，感谢你的到来。<br />
          你是第 {this.state.id}/{this.state.count} 个访客。<br />
          我是杨洽， &copy; 2019 感动自己。
        </p>
        <p>
          在98%的时间里我有常人无法体会的快乐，<br />
          在02%的时间里我有常人难以理解的忧愁。<br />
          这里会随意的放一些东西～
        </p>
        <p>
          写<Link to="/code">代码</Link>应该会是我的职业，<br />
          主要写前端和PHP，<br />
          在远程团队待了几年，<br />
          今年暑假决定去某大厂当个啦啦队员。
        </p>
        <p>
          我听一些<Link to="/music">歌</Link>，包括但不限于后朋、后摇、金属、民谣、说唱、电子。<br />
          以上排名可能有先后。<br />
          我用自己的钱混livehouse。
        </p>
        <p>
          我在学素描，<br />
          想用黑白表达更多的色彩，<br />
          没有努力，坚持下去的动力是交了不少学费。<br />
          这里有些<Link to="/picture">图</Link>（不只是画）。
        </p>
        <p>
          最后可能会写点乱七八糟的<Link to="/article">文字</Link>。
        </p>
        <p>
          其他一些在网上你可以找到我的地方: <br />
          Github: <a href="https://github.com/Xilesun" target="_blank">Xilesun</a> <br />
          豆瓣: <a href="https://www.douban.com/people/152839400/" target="_blank">-</a> <br />
          邮箱: <a href="mailto:2013xile@gmail.com">2013xile#gmail.com</a> <br />
        </p>
      </div>
    )
  }
}

export default Home;
